import { Component } from 'react';
import { connect } from 'react-redux';

import { apiActions, loadingActions } from './state/actions';

import { Layout } from 'antd';
import './App.css';

import AppHeader from './components/Header.jsx';
import Loader from './components/Loader.jsx';
import DataInput from './components/Form.jsx';
import DataDisplay from './components/Table.jsx';
import AppFooter from './components/Footer.jsx';

// const moralis = require('moralis');

const { setApi } = apiActions;
const { setLoaded, setPercentLoaded } = loadingActions;

const { Content } = Layout;

// Websockets Article - https://blog.logrocket.com/websockets-tutorial-how-to-go-real-time-with-node-and-react-8e4693fbf843/

class App extends Component {
  constructor(props) {
    super(props);
  }

  static getDerivedStateFromProps(props) {
    const { api, blockchain, endpoint, loading, percentLoaded, currency } =
      props;
    return {
      api,
      blockchain,
      endpoint,
      loading,
      percentLoaded,
      currency,
    };
  }

  componentDidMount() {
    console.log('App.jsx - componentDidMount()');
    this.props.setApi(this.state.endpoint);
    this.props.setPercentLoaded(50);
  }

  componentDidUpdate() {
    console.log('App.jsx - componentDidUpdate()');
  }

  render() {
    const { loading, percentLoaded } = this.state;
    return (
      <Layout style={{ minHeight: '100vh' }}>
        <Layout className="site-layout">
          <AppHeader className="site-layout-background" />
          <Content style={{ margin: '0 1rem' }}>
            {percentLoaded < 50 ? (
              <Loader />
            ) : (
              <div
                className="site-layout-background"
                style={{ padding: 24, minHeight: 360 }}>
                <DataInput />
                <DataDisplay />
              </div>
            )}
          </Content>
          <AppFooter style={{ textAlign: 'center' }} />
        </Layout>
      </Layout>
    );
  }
}

const mapStateToProps = (state) => {
  const { apiReducers, loadingReducers, polkadotReducers } = state;
  const { api, blockchain, endpoint } = apiReducers;
  const { loading, percentLoaded } = loadingReducers;
  const { currency } = polkadotReducers;
  return {
    api,
    blockchain,
    endpoint,
    loading,
    percentLoaded,
    currency,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    setApi: (data) => {
      dispatch(setApi(data));
    },
    setLoaded: () => {
      dispatch(setLoaded());
    },
    setPercentLoaded: (data) => {
      dispatch(setPercentLoaded(data));
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
