const mapStateToProps = (state) => {
  const { apiReducers, loadingReducers, polkadotReducers } = state;
  const { api, blockchain, endpoint } = apiReducers;
  const { block } = polkadotReducers;
  const { endBlock, genesisBlock, lastBlock, lastFinalizedBlock, startBlock } =
    block;
  const { loading, percentLoaded } = loadingReducers;
  return {
    api,
    blockchain,
    endBlock,
    endpoint,
    genesisBlock,
    lastBlock,
    lastFinalizedBlock,
    loading,
    percentLoaded,
    startBlock,
  };
};

const mapDispatchToProps = (dispatch) => {
  // Are these actually state because there's multiple & they constantly change?
  // setCurrentBlock
  // setLastBlock
  // setLastFinalizedBlock
  // setBlockHash
  // setBlockNumber
  return {
    addColumns: (data) => {
      dispatch(addColumns(data));
    },
    addRow: (data) => {
      dispatch(addRow(data));
    },
    addRows: (data) => {
      dispatch(addRows(data));
    },
    clearTable: () => {
      dispatch(clearTable());
    },
    setColumns: (data) => {
      dispatch(setColumns(data));
    },
    setLoaded: () => {
      dispatch(setLoaded());
    },
    setLoading: () => {
      dispatch(setLoading());
    },
    setPercentLoaded: (data) => {
      dispatch(setPercentLoaded(data));
    },
  };
};

// export default DataInput;

export default connect(mapStateToProps, mapDispatchToProps)(DataInput);
